import React, { useState } from "react";
import { authService } from "fbase";
import { message } from "antd";


const AuthForm = () => {
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [passwordConfirm, setPasswordConfirm] = useState("");
    const [newAccount, setNewAccount] = useState(false);
    const [error, setError] = useState("");

    const onChange = (event) => {
        const { target: { name, value } } = event;
        if (name === "email") {
            setEmail(value);
        } else if (name === "password") {
            setPassword(value);
        } else if (name === "passwordConfirm") {
            setPasswordConfirm(value);
        }
    };

    const onSubmit = async (event) => {
        event.preventDefault();
        try {
            if (newAccount) {
                if (password != passwordConfirm) {
                    setError("비밀번호가 일치하지 않습니다.");
                    return;
                }
                await authService.createUserWithEmailAndPassword(email, password);
                return message.success("계정이 성공적으로 생성되었습니다.");
            } else {
                await authService.signInWithEmailAndPassword(email, password);
            }
        } catch (error) {
            setError(error.message);
        }
    };

    const toggleAccount = () => {
        setError("");
        setNewAccount((prev) => !prev)
    };

    return (
        <>
            <form onSubmit={onSubmit} className="container">
                <input name="email" type="email" placeholder="E-mail" required value={email} onChange={onChange} className="authInput" />
                <input name="password" type="password" placeholder="Password" required value={password} onChange={onChange} className="authInput" />
                {
                    newAccount && (
                        <input name="passwordConfirm" type="password" placeholder="Password Confirm" required value={passwordConfirm} onChange={onChange} className="authInput" />
                    )
                }
                <input type="submit" className="authInput authSubmit" value={newAccount ? "회원가입" : "로그인"} />
                {error && <span className="authError">{error}</span>}
            </form>
            <span onClick={toggleAccount} className="authSwitch" >
                {newAccount ? "이미 등록된 계정이 있으신가요?" : "새로운 계정을 등록하시나요?"}
            </span>
        </>
    );
};

export default AuthForm;